.webdesign-workshop {
	display: inline-block;
}

.webdesign-workshop input {
	background: #222;
	background: -webkit-gradient(linear, left top, left bottom, from(#333), to(#222));
	background: -webkit-linear-gradient(#333, #222);
	background: -o-linear-gradient(#333, #222);
	background: linear-gradient(#333, #222);
	border: 1px solid #444;
	border-radius: 5px;
	-webkit-box-shadow: 0 2px 0 #000;
	        box-shadow: 0 2px 0 #000;
	color: #888;
	font-family: 'Cabin', helvetica, arial, sans-serif;
	font-size: 13px;
	font-weight: 400;
	height: 40px;
	margin: 20px;
	padding: 0 10px;
	text-shadow: 0 -1px 0 #000;
  display: block;
}

.ie .webdesign-workshop input {
	line-height: 40px;
}

.webdesign-workshop input::-webkit-input-placeholder {
   color: #888;
}

.webdesign-workshop input:-moz-placeholder {
   color: #888;
}

.webdesign-workshop input:focus {
	-webkit-animation: glow 800ms ease-out infinite alternate;
	        animation: glow 800ms ease-out infinite alternate;
	background: #222922;
	background: -webkit-gradient(linear, left top, left bottom, from(#333933), to(#222922));
	background: -webkit-linear-gradient(#333933, #222922);
	background: -o-linear-gradient(#333933, #222922);
	background: linear-gradient(#333933, #222922);
	border-color: #393;
	-webkit-box-shadow: 0 0 5px rgba(0,255,0,.2), inset 0 0 5px rgba(0,255,0,.1), 0 2px 0 #000;
	        box-shadow: 0 0 5px rgba(0,255,0,.2), inset 0 0 5px rgba(0,255,0,.1), 0 2px 0 #000;
	color: #efe;
	outline: none;
}

.webdesign-workshop input:focus::-webkit-input-placeholder {
	color: #efe;
}

.webdesign-workshop input:focus:-moz-placeholder {
	color: #efe;
}


.webdesign-workshop button {
	background: #222;
	background: -webkit-gradient(linear, left top, left bottom, from(#333), to(#222));
	background: -webkit-linear-gradient(#333, #222);
	background: -o-linear-gradient(#333, #222);
	background: linear-gradient(#333, #222);
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	border: 1px solid #444;
	border-left-color: #000;
	border-radius: 5px;
	-webkit-box-shadow: 0 2px 0 #000;
	        box-shadow: 0 2px 0 #000;
	color: #fff;
	font-family: 'Cabin', helvetica, arial, sans-serif;
	font-size: 13px;
	font-weight: 400;
	height: 40px;
	margin: 0;
	padding: 0;
	position: relative;
	text-shadow: 0 -1px 0 #000;
	max-width: 80px;
  width: 100px;
  min-width: 130px;
}

.webdesign-workshop button:hover,
.webdesign-workshop button:focus {
	background: #292929;
	background: -webkit-gradient(linear, left top, left bottom, from(#393939), to(#292929));
	background: -webkit-linear-gradient(#393939, #292929);
	background: -o-linear-gradient(#393939, #292929);
	background: linear-gradient(#393939, #292929);
	color: #5f5;
	outline: none;
}

.webdesign-workshop button:active {
	background: #292929;
	background: -webkit-gradient(linear, left top, left bottom, from(#393939), to(#292929));
	background: -webkit-linear-gradient(#393939, #292929);
	background: -o-linear-gradient(#393939, #292929);
	background: linear-gradient(#393939, #292929);
	-webkit-box-shadow: 0 1px 0 #000, inset 1px 0 1px #222;
	        box-shadow: 0 1px 0 #000, inset 1px 0 1px #222;
	top: 1px;
}








